.video-responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 0;
  }
  
  .video-responsive iframe {
    top: 0;
    height: 90%;
    width: 90%;
    position: absolute;
  }
  